const DataDisplay = () => {
  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  if (firebaseUser === null){
    NavigationService.navigate("Auth")
  }

  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)

  const CurrentData = useSelector((state) => state.firebase.currentData)
  /*if (!CurrentData || CurrentData.type){
    console.log("??")
    NavigationService.navigate("UserPannel")
  }*/

  const localDataStore = useSelector((state) => state.firebase.localDataStore)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)

  var DataView = (<View></View>);

  switch(CurrentData.type){
    case DataRefs.PENS:
      DataView = PenData_View(CurrentData, localDataStore)
      break;
    case DataRefs.PIGS:
      DataView = PigData_View(CurrentData, localDataStore)
      break;
  }

  /*const dispatch = useDispatch()
  useEffect(() => {
  }, [])*/
  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}
        {DataView}
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

function format_num(num){
  if (num < 10){
    num = "0"+num
  }
  return num;
}
function format_Time(time){
  var s = new Date(parseInt(time));
  var datetime = format_num(s.getDate()) + "/" + format_num(s.getMonth()) + "/" + s.getFullYear() + " " 
                + format_num(s.getHours()) + ":" + format_num(s.getMinutes()) + ":" + format_num(s.getSeconds());
  return datetime
}

const PenData_View = (CurrentData, localDataStore) => {
  var Pens;
  var Weighings = [];
  if (localDataStore && localDataStore.pens) {
    Pens = sortObject(localDataStore.pens)
    if (Pens[CurrentData.dataId]){
      CurrentData.data = Pens[CurrentData.dataId]
    }
  }
  
  if (localDataStore && localDataStore.weighings){
    if (localDataStore.weighings[CurrentData.dataId]){
      Weighings = localDataStore.weighings[CurrentData.dataId];
    }
  }
  
  if (CurrentData.data && !CurrentData.data.datas) CurrentData.data.datas = {}

  const dispatch = useDispatch()
  useEffect(() => {
    //Reducer updates at first dispatch to finish.. and then the view updates, not caring if the other dispatch finished..
    dispatch(FirebaseActions.fetchStarting(Actions.FETCH, (DataRefs.PENS + DataRefs.WEIGHING), [Object.keys(localDataStore.userData.farms)[0], CurrentData.dataId]))
  }, [dispatch])
  if (!CurrentData.data) {
    return(
      <View style={[Helpers.fillRowCenter, Helpers.center, Helpers.logoContainer]}>
        <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
      </View>
    )
  }
  return(
    <View style={[Helpers.contentContainer, Helpers.fillColCenter, Helpers.fullSize, Helpers.fill]}>
      <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin] }>
          <CustomButton Text={CurrentData.dataId} buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
            onPress={() => {} }/>
      </View>
      
      <View style={[Helpers.fullWidth]}>
        <CustomContentProp ingrid={true} texts={["Pig count", "Age", "Section"]} datas={[CurrentData.data.datas.count?CurrentData.data.datas.count:0,CurrentData.data.datas.age, CurrentData.data.section]} />
        <CustomContentProp ingrid={true} texts={["Batch", "Growth Cruve"]} datas={[CurrentData.data.datas.batch, CurrentData.data.datas.gcurve?CurrentData.data.datas.gcurve:"default"]} />
      </View>
      <View style={[Helpers.fullWidth, {height:20}]}/>
      <View style={[Helpers.fullWidth, Metrics.bottomMargin, {height:200}]}>
        <View style={[Helpers.borderContainer, Metrics.smallBottomMargin, {borderRadius:0, alignItems:"center", justifyContent:"center"}]}>
          <Text>Last weighings</Text>
        </View>
        <FlatList
          data={Object.keys(Weighings).reverse()}
          renderItem={({item}) => {
            if (item === "__jsogObjectId") return null;
            return(
              <Row height={35}>
                <Col>
                  <View style={[Helpers.borderContainer, {borderRadius: 0, alignItems:"center",justifyContent:"center"}]}>
                    <Text>{format_Time(Weighings[item].time)}</Text>
                  </View>
                </Col>
                <Col size={0.1}/>
                <Col>
                  <View style={[Helpers.borderContainer, {borderRadius: 0, alignItems:"center",justifyContent:"center"}]}>
                    <Text>{Weighings[item].weight}</Text>
                  </View>
                </Col>
              </Row>
            )}
          }
            //Setting the number of column
            numColumns={1}
            keyExtractor={(item, index) => index}
          />
      </View>

      <View style={[Helpers.borderContainer, Helpers.midWidth]}>
        <OperationsPanel farm={Object.keys(localDataStore.userData.farms)[0]} penid={CurrentData.dataId}/>
      </View>
    </View>
  )
}

