import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, Dimensions } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { LineChart } from 'react-native-chart-kit'
import { CustomButton, CustomListItem } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

import { Col, Row, Grid } from "react-native-easy-grid";

function random_rgba() {
  var o = Math.round, r = Math.random, s = 255;
  return 'rgba(' + o(r()*s) + ',' + o(r()*s) + ',' + o(r()*s) + ',' + r().toFixed(1) + ')';
}

function WeightsToGraphData(Legends, PenWeighings){
  var DataSets = []

  Legends.forEach(Pen => {
    if (PenWeighings[Pen]){
      var Weighings = PenWeighings[Pen];
      if (Object.keys(Weighings).length >= 1){
        var ran = random_rgba()

        var DataSet = []
        Object.keys(Weighings).forEach(Weighing => {
          var data = Weighings[Weighing]
          if (data.weight){
            DataSet.push(data.weight)
          }
        })
        if (DataSet.length > 0){
          DataSet.push(null)
          DataSet.push(null)
          DataSet.push(null)
          DataSet.push(null)
          DataSets.push({data: DataSet, color:(opacity = 1) => ran})
        }
        
      }
    }
  });
  return DataSets;
}

const DataDisplay = () => {
  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  if (firebaseUser == null){
    NavigationService.navigate("Auth")
  }

  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)
  const localDataStore = useSelector((state) => state.firebase.localDataStore)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)

  var Legends = []
  var DataSets = []
  var Labels = []

  var Graphs = null

  if (localDataStore && localDataStore.graphs) {
    Graphs = localDataStore.graphs;
    if (Graphs.gcurve){
      var Graph = Graphs.gcurve
      Legends.push("Batch template")
      DataSets.push({
        data: Graph.data,
        color: (opacity = 0.1) => `rgba(0, 0, 0, ${opacity})`, // optional
      })
      Labels = Graph.labels // Agressive overtake...
    }
    //console.log("Dataplot ", Graphs)
  }

  if (localDataStore && localDataStore.weighings) {
    Legends = Legends.concat(Object.keys(localDataStore.weighings).reverse())
    Legends = Legends.filter(e => e !== '__jsogObjectId');
    DataSets = DataSets.concat(WeightsToGraphData(Legends, localDataStore.weighings))
  }

  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(FirebaseActions.fetchStarting(Actions.FETCH, (DataRefs.GRAPH + DataRefs.WEIGHING), [Object.keys(localDataStore.userData.farms)[0], "default", 99]))
  }, [])

  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}
        <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin,] }>
          <CustomButton Text="Batch" buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
            onPress={() => {} }/>
        </View>
        <ScrollView horizontal={true} showsHorizontalScrollIndicator={false} style={[Helpers.fullSize]}>
          {Graphs ?(
          <LineChart
            //bezier
            style={[{marginRight:Metrics.normal}]}
            width={((Dimensions.get("window").width - (Metrics.normal * 2))*2) /2.05} //2.05 sets for pc screen..
            height={Dimensions.get("window").height - (Metrics.medium * 7)}

            data={{
              legend: Legends.slice(0, DataSets.length),
              datasets: DataSets,
              labels: Labels,
            }}

            withShadow={false}
            withVerticalLines={true}
            fromZero={false}
            segments={10}
            
            verticalLabelRotation={0} //Not Working in browser
            xLabelsOffset={0}

            chartConfig={{
              backgroundColor: Colors.white,
              backgroundGradientFrom: Colors.white,  //#FFFFFF
              backgroundGradientTo: Colors.white,     
              decimalPlaces: 0, // optional, defaults to 2dp
              color: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`,  //Lines / Grid
              labelColor: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`, //Lables
              style: {
                borderRadius: 16,
              },
              propsForDots: {
                r: "4",
                strokeWidth: "0.5",
                stroke: Colors.white
              }
            }}
          />):(<></>)}
        </ScrollView>
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

const styles = StyleSheet.create({
  circle: {
    width: 125,
    height: 125,
    borderRadius: 125/2,
    marginTop: Metrics.tiny,
    marginLeft: Metrics.tiny,
  },
});

export default DataDisplay;
