import React from 'react';
import { StyleSheet, View, TouchableOpacity, Text} from 'react-native';
import { ListItem } from 'react-native-elements'

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors } from '../Theme'

const CustomListItem = props => {
  const key = props.key? props.key : 0 //TODO: always set prop.. (else it throws warn)
  return (
    <ListItem key={key} containerStyle={{...styles.listitem, ...props.itemStyle}} button onPress={props.onPress}>
      <ListItem.Content>
        <ListItem.Title>{props.Title}</ListItem.Title>
        <ListItem.Subtitle>{props.Subtitle}</ListItem.Subtitle>
      </ListItem.Content>
    </ListItem>
  )
};

const styles = StyleSheet.create({
  listitem: {
    borderWidth: 1,
    borderColor: "#000",
    borderRadius: 25, // adds the rounded corners
    backgroundColor: '#fff',
    paddingVertical: 10,
    paddingHorizontal: 25,
  },
});

export default CustomListItem;