import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, Image } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { CustomButton } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'

const UserLoginScreen = () => {
  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  if (firebaseUser !== null){
    NavigationService.navigate("UserLoadingScreen")
  }

  const [Email, setEmail] = React.useState("")
  const [Pass, setPass] = React.useState("")

  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)
  const dispatch = useDispatch()

  useEffect(() => {
  }, [])
  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}
        
        <View style={[Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
          <Input
            label="Username"
            style={ApplicationStyles.input}
            onChangeText={text => {setEmail(text)}}
            value={Email}
          />
          <Input
            label="Password"
            style={ApplicationStyles.input}
            onChangeText={text => {setPass(text)}}
            value={Pass}
          />
          <CustomButton Text="Login" icon={Images.User}
            onPress={() => {
              if (!Email || Email === "") dispatch(FirebaseActions.setFirebaseError("Email is needed"))
              else if (!Pass || Pass === "") dispatch(FirebaseActions.setFirebaseError("Password is needed"))
              else {
                dispatch(FirebaseActions.fetchStarting(Actions.SIGN, DataRefs.USER, [Email, Pass]))
              }
            }}/>
        </View>

        <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
          <CustomButton Text="Create New User"
            onPress={() => {
              dispatch(FirebaseActions.setFirebaseError(null))
              NavigationService.navigate("CreateNewUser")
            }}/>
        </View>
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

export default UserLoginScreen;
