async function is_getToken() {
  var fcmToken = ""
  try{
    fcmToken = await firebase.messaging().getToken();
    console.log('Device_token: ', fcmToken)
  }catch(error){
    console.log(error)
  }
  return fcmToken;
}

function requestFirebaseToken() {
  return new Promise((resolve, reject) => {
    var token;
    try {
      token = is_getToken();
    } catch{}

    if (!token) {
      resolve('Could not find Firebase Token')
    }
    resolve(token)
  })
}

function createFirebaseUserWithemailandpassword(email, password){
  return new Promise((resolve, reject) => {
    firebase.auth().createUserWithEmailAndPassword(email,password).then(() => {
      const user = firebase.auth().currentUser;
      const ref = "users/" + user.uid;
      firebase.database().ref(ref).set({
        name: user.displayName ? user.displayName : user.email,
        state: UserStates.STARTER,
      }).then(() => {
        resolve(null)
      }).catch(error => {
        console.log("UserCreate database Error: ", error)
        resolve({error:'Database error'})
      })
    }).catch(error => {
      console.log("UserCreate Error: ", error);

      if (error.code === 'auth/email-already-in-use') {
        resolve({error:'The email address is already in use.'})
      }
      if (error.code === 'aith/invalid-email') {
        resolve({error:'The email addres is invalid.'})
      }
    })
  })
}

function signinWithemailandpassword(email, password){
  if (firebase.auth().currentUser){
    if (firebase.auth().currentUser.email != email){
      const res = logoutUser()
    }
    else {
      return new Promise((resolve, reject) => {
        resolve(true)
      })
    }
  }
  return new Promise((resolve, reject) => {
    firebase.auth().signInWithEmailAndPassword(email,password).then(() => {
      resolve(true)
    }). catch(error => {
      console.log("User Signin Error: ", error);

      if (error.code === 'auth/email-already-in-use') {
        resolve('The email address is already in use.')
      }
      if (error.code === 'aith/invalid-email') {
        resolve('The email addres is invalid.')
      }
      else {
        resolve('Could not singin')
      }
    })
  })
}

function logoutUser(){
  return new Promise((resolve, reject) => {
    firebase.auth().signOut().then(() => {
      resolve(true)
    })
  })
}

function updateUserData({newData}) {
  const user = firebase.auth().currentUser;
  const ref = "users/" + user.uid;

  return new Promise((resolve, reject) => {
    firebase.database().ref(ref).update(
      newData
    ).then(() => {
      resolve(true)
    }).catch(error => {
      if (error.code === 'database/permission-denied') {
        console.log("User update was permission denied. ");
      }else if (error.code) {
        console.log("User update Error: ", error);
      }

      resolve(false)
    })
  })
}

function updateFarmData({farmid, newData}) {
  const ref = "farms_data/" + farmid;
  
  return new Promise((resolve, reject) => {
    firebase.database().ref(ref).update(
      newData
    ).then(() => {
      resolve(true)
    }).catch(error => {
      if (error.code === 'database/permission-denied') {
        console.log("Farm data update was permission denied. ");
      }else if (error.code) {
        console.log("User update Error: ", error);
      }

      resolve(false)
    })
  })
}

function createFarm({farmData}){
  const user = firebase.auth().currentUser;
  const ref = "users/" + user.uid;

  return new Promise((resolve, reject) => {
    const farm = firebase.database().ref('farms/').push()
    const farmid = farm.key
    
    farm.set({
      email: user.email,
      manager: user.displayName ? user.displayName : user.email,
      name: farmData.name
    }).then(() => {
      resolve(farmid)
    }).catch(error => {
      console.log("Create Farm Error: ", error);
      resolve(false)
    })
  })
}

function fetchStarting(action, params){
  switch(action){
    case DataRefs.USERS:
      return fetchUser(params);
    case DataRefs.FARMS:
      return fetchUserFarm(params);
    case DataRefs.SECTIONS:
      return fetchUserSections(params); 
    case DataRefs.PENS:
      return fetchUserPens(params[0], params[1]);
    case DataRefs.PIGS:
      return fetchUserPigs(params);
    case DataRefs.WEIGHING:
      return fetchUserPenWeights(params[0], params[1]);
    case DataRefs.GRAPH:
      return fetchUserGraphData(params[0], params[1]);

    //TODO find way to double call.. ?
    case DataRefs.PENS + DataRefs.WEIGHING:
      return new Promise((resolve, reject) => {
        var Data = {}
        fetchUserPens(params[0], params[1]).then((pens) =>{
          Data = {...Data, ...pens}
          fetchUserPenWeights(params[0], params[1]).then((weights) => {
            Data = {...Data, ...weights}
            resolve(Data)
          }).catch(error => {
            resolve(Data)
          })
        }).catch(error => {
          resolve(Data)
        })
      })
    case DataRefs.SECTIONS + DataRefs.WEIGHING:
      return new Promise((resolve, reject) => {
        var Data = {}
        fetchUserSections(params[0]).then((sections) =>{
          Data = {...Data, ...sections}
          
          var itemsProcessed = 0;
          var length = 0;
          Object.keys(Data.sections).forEach(section => {
            length += Object.keys(Data.sections[section].pens).length
            Object.keys(Data.sections[section].pens).forEach(pen => {
              var limit = (params[1] && !isNaN(params[1])?params[1]:1)
              fetchUserPenWeights(params[0], pen, 0, limit).then((weights) => {
                itemsProcessed++
                var newWeights = {...Data.weighings, ...weights.weighings}
                Data = {...Data, weighings:newWeights}

                if (itemsProcessed === length){
                  resolve(Data)
                }
              }).catch(error => {
                console.log(error)
                resolve(Data)
              })
            })

          })
        }).catch(error => {
          console.log(error)
          resolve(null)
        })
      })
    case DataRefs.GRAPH + DataRefs.WEIGHING:
      return new Promise((resolve, reject) => {
        var Data = {}
        fetchUserGraphData(params[0], params[1]).then((Graphs) => {
          Data = {...Data, ...Graphs}
          fetchStarting(DataRefs.SECTIONS + DataRefs.WEIGHING, [params[0],params[2]]).then((_Data) => {
            Data = {...Data, ..._Data}
            resolve(Data)
          }).catch(error => {
            console.log(error)
            resolve(Data)
          })
        }).catch(error => {
          resolve(null)
        })
      })

    default:
      return new Promise((resolve, reject) => {
        console.log("fecth unknown", action);
        resolve(null)
      })
  }
}

function createStarting(action, params){
  switch(action){
    case DataRefs.USERS:
      return createFirebaseUserWithemailandpassword(params[0],params[1])
    
    case DataRefs.PIGS:
      return createNewPig(params[0], params[1], params[2], params[3]);
    case DataRefs.WEIGHING:
      return createPenWeight(params[0], params[1], params[2]);
    case DataRefs.PENS:
      console.log(params)
      if (params.length >= 4){
        var ref = "farms_data/" + params[0] + "/";
        if (params[3] == Actions.DELETE) {
          return new Promise((resolve, reject) => {
            firebase.database().ref(ref + "pens/" + params[2]).remove().then(() => {
              firebase.database().ref(ref + "sections/" + params[1] + "/pens/" + params[2]).remove().then(() => {
                resolve(null)
              })
            })
          })
        }
      }else {
        return createNewPen(params[0], params[2], params[1]);
      }
    default:
      return new Promise((resolve, reject) => {
        console.log("create unknown", action);
        resolve(null)
      })
  }
}

//** Fetch Commands */
function fetchUser(User){
  const user = User?User : firebase.auth().currentUser;
  if (user.uid) {
    const ref = "users/" + user.uid;
    return firebase.database().ref(ref).once('value').then((userData) => {
      return userData.val()
    })
  }
  else return new Promise((resolve, reject) => {
    console.log("User uid in fecth unknown");
    resolve(null)
  })
}

function fetchUserFarm(farmid){
  var ref = "farms";
  if (farmid){
    ref += "/" + farmid
    return firebase.database().ref(ref).once('value').then((_farmData) => {
      //TODO:Multiple farms
      return {farms: _farmData.val()}
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Farm Error");
    resolve(null)
  })
}

function fetchUserSections(farmid, sectionid){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/sections";
    if (sectionid){
      ref += "/" + sectionid
    }
    return firebase.database().ref(ref).once('value').then((_sectionidData) => {
      return {sections: _sectionidData.val()}
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pigs Error");
    resolve(null)
  })
}

function fetchUserPens(farmid, penid){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/pens";
    if (penid){
      ref += "/" + penid
    }
    return firebase.database().ref(ref).once('value').then((_pensData) => {
      if (penid) {
        var penData = _pensData.val()
        if (!penData.datas) penData.datas = {}
        if (!penData.datas.gcurve) penData.datas.gcurve = "default";

        var pen = {[penid]: penData};
        return {pens: pen}
      } else {
        return {pens: _pensData.val()}
      }
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pens Error");
    resolve(null)
  })
}

function fetchUserPigs(farmid, tagid){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/pigs";
    if (tagid){
      ref += "/" + tagid
    }

    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).once('value', snapshot => {
        var pigs = {[snapshot.key]:{}}
        var pig = {}
        snapshot.forEach(c => {
          pig[c.key] = c.val()
          pigs[snapshot.key] = pig;
        })

        if (tagid){
          resolve({pigs: pigs})
        }else {
          resolve(pigs)
        }
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pigs Error");
    resolve(null)
  })
}

function format_num(num){
  if (num < 10){
    num = "0"+num
  }
  return num;
}
function format_Time(time){
  var s = new Date(parseInt(time));
  var datetime = format_num(s.getDate()) + "/" + format_num(s.getMonth()) + "/" + s.getFullYear() + " " 
                + format_num(s.getHours()) + ":" + format_num(s.getMinutes()) + ":" + format_num(s.getSeconds());
  return datetime
}

function fetchUserPenWeights(farmid, penid, limitStart = 0, limitTo = 5){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/weighings";
    if (penid) {
      ref += "/" + penid
      //... set pen datas newest weighing.. ??

      return new Promise((resolve, reject) => {
        firebase.database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', snapshot => {
          var weighing = {[snapshot.key]:{}}
          snapshot.forEach(c => {
            weighing[snapshot.key][c.key] = c.val()
          })
          resolve({weighings: weighing})
        })
      })
    }
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pens Error");
    resolve(null)
  })
}

function fetchUserGraphData(farmid, graphid){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/graph";
    if (graphid) {
      ref = ref + "/" + graphid;
      return new Promise((resolve, reject) => {
        firebase.database().ref(ref).once('value').then((graph) => {
          var GraphVal = graph.val()
          if (GraphVal) {
            console.log("test fetch user graph", GraphVal)
            resolve(null)
          } else {
            if (graphid === "default") {
              graphid = "gcurve"
            }
            firebase.database().ref("farms_data/Agrisys/graph/" + graphid).once('value').then((Agraph) => {
              var AgraphVal = Agraph.val();
              if (AgraphVal) {
                var Graphs = {[Agraph.key]: AgraphVal}
                resolve({graphs: Graphs})
              }else {
                resolve(null)
              }
            }).catch(error => {
              console.log("Get Agrisys graph Error: ", error);
              resolve(null)
            })
          }
        }).catch(error => {
          console.log("Get farm graph Error: ", error);
          resolve(null)
        })
      })
    }
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Graph Error");
    resolve(null)
  })
}
