function createNewPen(farmid, penid, sectionid){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/";
    return new Promise((resolve, reject) => {

      if (penid){
        var ref2 = ref + "pens/" + penid + "/"
        firebase.database().ref(ref2).set({
          section: sectionid,
        }).then(() => {
          firebase.database().ref(ref + "sections/" + sectionid + "/pens").update({
            [penid]: true,
          }).then(() => {
            resolve(null)
          });
        });
      }
    })
  }
}

function updatePigPen(farmid, tagid, penid, action, resolve){
  if (penid){
    var ref2 = "farms_data/" + farmid + "/" + "pens/" + penid + "/datas"
    firebase.database().ref(ref2 + "/count").once('value').then((counts) => {
      var val = counts.val()
      if (action && !isNaN(action)){
        val = parseInt(val) + parseInt(action)
      }else {
        val = parseInt(val) +1
      }
      firebase.database().ref(ref2).update({
        count: val,
      }).then(() => {
        fetchUserPens(farmid, penid).then((pen)=>{
          resolve(pen)
        })
      });
    }).catch(error => {
      console.log("Create new pig Error: ", error);
      resolve(null)
    })
  }
}

function createNewPig(farmid, tagid, penid, action){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/";
    return new Promise((resolve, reject) => {

      if (tagid){
        var ref2 = ref + "pigs/" + tagid + "/"
        firebase.database().ref(ref2).set({
          pen: penid ? penid : 0,
        }).then(() => {
          console.log("Firebase Service.. not tested..")
          fetchUserPigs(farmid, tagid).then((pig)=>{
            resolve(pig)
          })
        });
      }else {
        updatePigPen(farmid, tagid, penid, action, resolve)
      }
    })
  }
}

function createPenWeight(farmid, penid, weightdata){
  if (farmid) {
    var ref = "farms_data/" + farmid + "/weighings";
    if (penid){
      ref += "/" + penid + "/"

      return new Promise((resolve, reject) => {
        const weightRef = firebase.database().ref(ref).push()
        const weightid = weightRef.key

        weightRef.set({
          time: firebase.database.ServerValue.TIMESTAMP,
          weight: weightdata,
        }).then(() => {
          fetchUserPenWeights(farmid, penid).then((weights)=>{
            resolve(weights)
          })
        }).catch(error => {
          console.log("Create Weight Error: ", error);
          resolve(null)
        })
      })
    }
  }
}


function wrappedAlert(title, message, okText, _cancelable) {
  return new Promise(resolve => {
    Alert.alert(
      title,
      message,
      [{text: okText, onPress: function() { resolve(); }}],
      {cancelable: _cancelable, onDismiss: function() {resolve(); }},
    );
  });
}

