import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView } from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { DataRefs, Actions } from '../../../Stores/Firebase/Selectors'

import { CustomButton, CustomContentProp } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

const Dashboard = () => {
  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  if (firebaseUser === null){
    NavigationService.navigate("Auth")
  }

  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)
  const localDataStore = useSelector((state) => state.firebase.localDataStore)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)

  //TODO: multiple Farms ??
  var farmName = "Not set"
  var farmManager = "Not set"
  var farmMail = "Not set"

  if (localDataStore && localDataStore.farms) {
    farmName = localDataStore.farms.name?localDataStore.farms.name: farmName
    farmManager = localDataStore.farms.name?localDataStore.farms.manager: farmManager
    farmMail = localDataStore.farms.name?localDataStore.farms.email: farmMail
  }

  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(FirebaseActions.fetchStarting(Actions.FETCH, DataRefs.FARMS, Object.keys(localDataStore.userData.farms)[0]))
  }, [])

  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.contentContainer, Helpers.fullWidth, Helpers.fill]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}
        
        <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin,] }>
          <CustomButton Text="Farm description" buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
            onPress={() => {} }/>
        </View>

        <View style={[Helpers.buttonContainer, Helpers.fullWidth, {height:110,}]}>
          <CustomContentProp texts={["Farm name", "Manager", "E-mail"]} datas={[farmName, farmManager,farmMail ]} />
        </View>
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

export default Dashboard;
