import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView } from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { CustomButton } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

const Operations = () => {
  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  if (firebaseUser === null){
    NavigationService.navigate("Auth")
  }
  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)
  const localDataStore = useSelector((state) => state.firebase.localDataStore)

  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(FirebaseActions.setCurrentData({}))
  }, [])
  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}
        <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin, {marginBottom:60}] }>
        </View>

        <View style={[Helpers.halvWidth, Helpers.fillColMain]}>
          <OperationsPanel farm={Object.keys(localDataStore.userData.farms)[0]} section={true}/>
        </View>
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

export const OperationsPanel = props => {
  const dispatch = useDispatch()
  return(
    <ScrollView style={{paddingRight: 5}}>
      <View style={[Helpers.buttonContainer]}>
        <CustomButton Text="Weight"
          onPress={() => NavigationService.navigate("WeighingOperations") }/>
        {props.farm && props.penid?(
          <View>
            <CustomButton Text="Add Pig"
              onPress={() => {
                dispatch(FirebaseActions.fetchStarting(Actions.CREATE, DataRefs.PIGS, [props.farm, null, props.penid, "1"]))
              } }/>
            <CustomButton Text="Move Pig"
              onPress={() => {
                dispatch(FirebaseActions.fetchStarting(Actions.CREATE, DataRefs.PIGS, [props.farm, null, props.penid, "-1"]))
              } }/>
          </View>
        ):(<></>)}
        {props.farm && props.section ? (
          <View style={[{marginTop:40}]}>
            <CustomButton Text="Add Pen"
              onPress={() => {
                dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, action:Actions.CREATE}))
                NavigationService.navigate("DataEditor")
              } }/>
            <CustomButton Text="Remove Pen"
              onPress={() => {
                dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, action:Actions.DELETE}))
                NavigationService.navigate("DataEditor")
              } }/>
          </View>
        ):(<></>)}
        {/*<CustomButton Text="Start New Batch"
          onPress={() => {} }/>*/}
      </View>
    </ScrollView>
  )
}

export default Operations;
