import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, Picker } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { CustomButton, CustomListItem } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

const DataEditor = () => {
  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  if (firebaseUser === null){
    NavigationService.navigate("Auth")
  }

  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)
  const localDataStore = useSelector((state) => state.firebase.localDataStore)

  const CurrentData = useSelector((state) => state.firebase.currentData)

  var DataView = (<View></View>);

  switch(CurrentData.type){
    case DataRefs.PENS:
      DataView = PenEditor_View(CurrentData, localDataStore)
      break;
  }

  /*const dispatch = useDispatch()
  useEffect(() => {
  }, [])*/
  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}
        {DataView}
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

const PenEditor_View = (CurrentData, localDataStore) => {
  const [Get, setGet] = React.useState(true)

  var Sections = { }
  if (localDataStore && localDataStore.sections) {
    Sections = sortObject(localDataStore.sections)
    if (CurrentData.action === Actions.CREATE) {
      Sections["New Section"] = {}
    }
  }

  const [Section, setSection] = React.useState("")
  const [Pens, setPens] = React.useState("")
  const [Pen, setPen] = React.useState("")

  const [NewSection, setNewSection] = React.useState(false)

  const dispatch = useDispatch()
  useEffect(() => {
    if (Get){
      dispatch(FirebaseActions.fetchStarting(Actions.FETCH, DataRefs.SECTIONS, [Object.keys(localDataStore.userData.farms)[0]]))
      setGet(false)
    } else if (Pen === "") {
      var _Section = Object.keys(Sections)[0]
      var _Pens = sortObject(Sections[_Section].pens)
      setSection(_Section)
      setPens(_Pens)
      setPen(Object.keys(_Pens)[0])
    }
    
    //Reducer updates at first dispatch to finish.. and then the view updates, not caring if the other dispatch finished..
    
  }, [dispatch])
  return(
    <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
      <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin] }>
        {CurrentData.action === Actions.CREATE ?(
          <CustomButton Text="Add Pen" buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
          onPress={() => {} }/>
        ):(<></>)}
        {CurrentData.action === Actions.DELETE ?(
          <CustomButton Text="Remove Pen" buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
          onPress={() => {} }/>
        ):(<></>)}
      </View>
      
      <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
        {NewSection && CurrentData.action === Actions.CREATE ?(
          <Input
            label="Section Name"
            style={{ ...Style.input}}
            onChangeText={text => {setSection(text)}}
            value={Section}
          />
        ):(
          <Picker
            mode="dropdown"
            selectedValue={Section}
            style={{ height: 50, width: 150 }}
            onValueChange={(itemValue, itemIndex) => {
              setSection(itemValue)
              if (itemValue === "New Section"){
                setNewSection(true)
                setSection("Section_" + Object.keys(Sections).length)
              } else {
                var _Pens = sortObject(Sections[itemValue].pens)
                setPens(_Pens)
                if (CurrentData.action === Actions.DELETE){
                  setPen(Object.keys(_Pens)[Object.keys(_Pens).length-1])
                }
                else {
                  setPen("Pen_" + (Object.keys(_Pens).length +1))
                }
              }
            }}>
            {Object.keys(Sections).map((Section, i) => {
                return (<Picker.Item label={Section} value={Section} key={i}/>)
            })}
          </Picker>
        )}
        {CurrentData.action === Actions.CREATE ?(
          <Input
            label="Pen name"
            style={{ ...Style.input}}
            onChangeText={text => {setPen(text)}}
            value={Pen}
          />
        ):(<></>)}
        {CurrentData.action === Actions.DELETE ?(
          <Picker
            mode="dropdown"
            selectedValue={Pen}
            style={{ height: 50, width: 150 }}
            onValueChange={(itemValue, itemIndex) => {
              setPen(itemValue)
            }}
          >
            {Object.keys(Pens).map((Pen, i) => {
                return (<Picker.Item label={Pen} value={Pen} key={i}/>)
            })}
          </Picker>
        ):(<></>)}
      </View>

      {CurrentData.action === Actions.CREATE || CurrentData.action === Actions.DELETE ?(
        <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
          <CustomButton Text="Submit"
            onPress={() => {
              if (!Section){
                dispatch(FirebaseActions.setFirebaseError("A section is needed"))
              } else if (!Pen || Pen === ""){
                dispatch(FirebaseActions.setFirebaseError("A pen name is needed"))
              } else if (!isNaN(Pen) || !isNaN(Pen[0])){
                dispatch(FirebaseActions.setFirebaseError("Pen name cannot be or start with a number"))
              }
              else {
                var exists = false;

                Object.keys(Sections).map((section, i) => {
                  if (Sections[section].pens){
                    var test = Object.keys(Sections[section].pens).filter(e => e === Pen).length
                    if (test) exists = true
                  }
                  //var _Pens = Sections[section].pens.filter(e => e.Name === 'Magenic').length > 0)
                })

                if (exists && CurrentData.action === Actions.CREATE){
                  dispatch(FirebaseActions.setFirebaseError("Given pen name already exists"))
                } else if (!exists && CurrentData.action === Actions.DELETE){
                  dispatch(FirebaseActions.setFirebaseError("Cannot remove non-existing pen"))
                }
                else {
                  dispatch(FirebaseActions.setFirebaseError(null))
                  dispatch(FirebaseActions.fetchStarting(CurrentData.action, DataRefs.PENS, [Object.keys(localDataStore.userData.farms)[0], Section, Pen]))
                  NavigationService.navigate("UserOperations")
                }
              }
            } }/>
        </View>
      ):(<></>)}
    </View>
  )
}

export default DataEditor;
