import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView, FlatList, TouchableOpacity, Image } from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { CustomButton, CustomListItem } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics, Images, Colors } from '../../../Theme'

import { Col, Row, Grid } from "react-native-easy-grid";
import { firebase } from '@react-native-firebase/auth'

const Pens = () => {
  const [Mid, setMid] = React.useState(0)

  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  if (firebaseUser === null){
    NavigationService.navigate("Auth")
  }

  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)
  const localDataStore = useSelector((state) => state.firebase.localDataStore)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)

  var Sections = null
  if (localDataStore && localDataStore.sections) {
    Sections = sortObject(localDataStore.sections)
  }

  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(FirebaseActions.fetchStarting(Actions.FETCH, (DataRefs.SECTIONS + DataRefs.WEIGHING), [Object.keys(localDataStore.userData.farms)[0]]))
    //dispatch(FirebaseActions.fetchStarting(Actions.FETCH, DataRefs.SECTIONS, Object.keys(localDataStore.userData.farms)[0]))
    if (localDataStore && localDataStore.weighings){
      var _Mid = 0;
      for (const property in localDataStore.weighings) {
        if (localDataStore.weighings[property]){
          var weight = localDataStore.weighings[property][Object.keys(localDataStore.weighings[property])[0]].weight
          if (weight){
            if (_Mid > 0){
              _Mid = ((_Mid + weight)/2)
            } else {
              _Mid = (_Mid + weight)
            }
          }
        }
      }
      setMid(_Mid)
    }
  }, [])
  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.fullSize, Helpers.fill]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}
        {firebaseUser && Sections? (
          /*<FlatList scrollEnabled={false}
            style={[Metrics.verticalMargin]}
            data={Object.keys(Sections).concat(Object.keys(Sections))}
            renderItem={({item}) => {
              var Pens = sortObject(Sections[item].pens)
              return(
                <Section name={item} pens={Pens} mid={Mid}/>
              )
            }}
            //Setting the number of column
            numColumns={1}
            keyExtractor={(item, index) => index}
          />*/
          <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
            {Object.keys(Sections).map((_Section, i) => {
              var Pens = sortObject(Sections[_Section].pens)
              return(
                <Section name={_Section} pens={Pens} mid={Mid}/>
              )
            })}
          </ScrollView>
        ):(
          <View style={[Helpers.fillRowCenter, Helpers.center, Helpers.logoContainer]}>
            <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
          </View>
        )}
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

const Section = props => {
  //var Secions = props.sections
  var Name = props.name
  var Pens = props.pens

  var _Rows = [];

  var _Row = [];
  Object.keys(Pens).forEach(pen => {
    if (_Row.length === 4){
      _Rows.push((<Row>{_Row}</Row>))
      _Row = [];
    }
    _Row.push(
      <Pen name={pen} mid={props.mid}/>
    )
  });

  while(_Row.length < 4){
    _Row.push(<Col></Col>)
  }
  _Rows.push((<Row>{_Row}</Row>))

  return(
    <View style={[Helpers.fullSize, Helpers.fill]}>
      <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
        <CustomButton Text={Name} buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
          onPress={() => {} }/>
      </View>
      <View>
        {_Rows}
      </View>
    </View>
  )
}

const Pen = props => {
  const localDataStore = useSelector((state) => state.firebase.localDataStore)
  var color = "white"
  var name = props.name

  if (localDataStore.weighings && localDataStore.weighings[name]){
    var Weighing = localDataStore.weighings[name][Object.keys(localDataStore.weighings[name])[0]];
      if (Weighing){
      var weight = localDataStore.weighings[name][Object.keys(localDataStore.weighings[name])[0]].weight
      if (weight && !isNaN(weight)){
        color = Colors.success

        var Mid = props.mid
        if (weight > Mid+10 || weight < Mid-10){
          color = Colors.error
        }
      }
    }
  }

  const dispatch = useDispatch()
  return (
    <Col>
      <TouchableOpacity style={[Helpers.fillColCenter, Helpers.borderContainer, {margin: 5, width:70, height:70, backgroundColor:color }]}
        onPress={() => {
          dispatch(FirebaseActions.setCurrentData({dataId:name, type:DataRefs.PENS}))
          NavigationService.navigate("DataDisplay")
        }}>
        <Text>{name}</Text>
      </TouchableOpacity>
    </Col>
  )
}

export default Pens;
