import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { CustomButton } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'

//TODO: make this standart for all Screens... Also in phone app..
class FrontScreen extends Component {
  constructor(props){
    super(props);
    this.state = {
      ISLOADPAGE: true,
    }
  }

  getData(){
    setTimeout(() => {
      this.props.setFirebaseError(null)

      /*console.log('Our data is fetched');
      this.setState({
        firebaseUser: useSelector((state) => state.firebase.firebaseUser),
        IsLoading: useSelector((state) => state.firebase.firebaseIsLoading),
        localDataStore: useSelector((state) => state.firebase.localDataStore),
      })*/
    }, 1000)
  }

  checkData(props){
    if (props.firebase.firebaseUser === null){
      NavigationService.navigate("LoginUser")
    }
  }

  componentDidMount(){
    this.getData();

    this.checkData(this.props);
  }

  componentDidUpdate(prevProps, prevState) {
    this.checkData(prevProps);
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    
    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {firebaseUser?(
          <View style={ Helpers.textContainer }>
            <Text style={ApplicationStyles.text}>Wellcome: {firebaseUser.displayName ? firebaseUser.displayName : firebaseUser.email}</Text>
          </View>):(<></>)}

          <View style={[Helpers.buttonContainer, Helpers.midWidth, {marginBottom:30}] }>
            <CustomButton Text="Dashboard" icon={Images.DashBoard}
              onPress={() => NavigationService.navigate("UserDashBoard") }/>
            <CustomButton Text="Pigs" icon={Images.Sow}
              onPress={() => NavigationService.navigate("UserPigs") }/>
            <CustomButton Text="Pens" icon={Images.Section}
              onPress={() => NavigationService.navigate("UserPens") }/>
            <CustomButton Text="Batchs" icon={Images.Curves}
              onPress={() => NavigationService.navigate("DataPlotter") }/>
            <CustomButton Text="Operations" icon={Images.System}
              onPress={() => NavigationService.navigate("UserOperations") }/>
          </View>
          
          <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
            <CustomButton Text="LogOut"
              onPress={() => {
                this.props.logOut()
              }}/>
          </View>
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

