export function* handleAuthState() {
  const authChannel = yield eventChannel((emit) => {
    const subscription = firebase.auth().onAuthStateChanged((user) => {
      if (user == null){
        emit("NONE")
      }else {
        emit(user)
      }
    })
    return () => {
      subscription()
    };
  }, buffers.expanding(1))

  try {
    for (;;) {
      var NewData = {localDataStore:{}}
      const newAuth = yield take(authChannel)
      NewData.firebaseUser = newAuth==="NONE"?null:newAuth;
      if (NewData.firebaseUser) {
        const userData = yield call(firebaseService.fetchStarting, DataRefs.USERS, newAuth)
        NewData.localDataStore.userData = userData;
      }

      yield put(FirebaseActions.fetchSuccess(NewData))
      console.log("New auth: ", NewData)
    }
  } finally {
    if (yield cancelled()) {
      authChannel.close()
    }
  }
}

export function* fetchStarting({action, ref, params}) {
  switch(action) {
    case Actions.FETCH:
      if (Object.values(DataRefs).includes(ref) || 
          ref === (DataRefs.PENS + DataRefs.WEIGHING) ||
          ref === (DataRefs.SECTIONS + DataRefs.WEIGHING) ||
          ref === (DataRefs.GRAPH + DataRefs.WEIGHING)) {
        const FetchData = yield call(firebaseService.fetchStarting, ref, params)
        yield put(FirebaseActions.fetchSuccess({localDataStore:FetchData}))
      }
      break;

    //TODO: reaplasce create new user and sing in / out
    case Actions.CREATE:
      if (Object.values(DataRefs).includes(ref)) {
        const NewData = yield call(firebaseService.createStarting, ref, params)
        if (NewData && NewData.error){
          yield put(FirebaseActions.fetchFailure(NewData.error))
        }else {
          yield put(FirebaseActions.fetchSuccess({localDataStore:NewData}))
        }
      }
      break;
    case Actions.MOVE:

      break;
    case Actions.DELETE:
      if (Object.values(DataRefs).includes(ref)) {
        //TODO: Mabye not use createStarting for delete
        params.push(action)
        const NewData = yield call(firebaseService.createStarting, ref, params)
        if (NewData && NewData.error){
          yield put(FirebaseActions.fetchFailure(NewData.error))
        }else {
          switch(ref){
            case DataRefs.PENS:
              yield put(FirebaseActions.fetchStarting(Actions.FETCH, DataRefs.PENS, [params[0]]))
              yield put(FirebaseActions.fetchStarting(Actions.FETCH, DataRefs.SECTIONS, [params[0]]))
              break;
            default:
              yield put(FirebaseActions.fetchSuccess({localDataStore:NewData}))
              break;
          }
        }
      }
      break;
    case Actions.SIGN:
      var firebaseUser = yield select((state) => state.firebase.firebaseUser)
      
      yield put(FirebaseActions.actionStarting())
      if (firebaseUser){
        const res = yield call(firebaseService.logoutUser)
        if (!res){
          yield put(FirebaseActions.fetchFailure("Could not LogOut"))
          return;
        }
      } else {
        const res = yield call(firebaseService.signinWithemailandpassword, params[0], params[1])
        if (typeof res === "string") {
          yield put(FirebaseActions.fetchFailure(res))
        }
      }
      yield put(FirebaseActions.fetchSuccess())
      break;
    default:
      break;
  }
}

// Notification Handler for then App is in Foreground State
export function* onMessageHandler() {
  const onMessageChannel = yield eventChannel((emit) => {
    const messageListener = firebase.messaging().onMessage((remoteMessage) => {
      //console.log("message ? ", remoteMessage);
      emit(remoteMessage);
    });
    return () => {
      messageListener();
    };
  }, buffers.expanding(1));

  try {
    for (;;) {
      const remoteMessage = yield take(onMessageChannel)
      console.log("New Firebase message: ", remoteMessage)
      if (remoteMessage.notification && remoteMessage.notification.title && remoteMessage.notification.body){
        yield call(firebaseService.wrappedAlert, remoteMessage.notification.title, remoteMessage.notification.body, "Ok", false)
      }else {
        console.log("New Firebase data message: ", remoteMessage)
      }
    }
  } finally {
    if (yield cancelled()) {
      onMessageChannel.close()
    }
  }
}

