import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'

import { CustomButton } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

import { UserStates } from '../../../Stores/Firebase/Selectors'

const CreateNewUser = () => {
  const [Farm, setFarm] = React.useState("Test")
  const [Sections, setSections] = React.useState("1")
  const [Pens, setPens] = React.useState("1")

  const firebaseUser = useSelector((state) => state.firebase.firebaseUser)
  const localDataStore = useSelector((state) => state.firebase.localDataStore)
  if (firebaseUser === null || !localDataStore.userData || localDataStore.userData.state != UserStates.STARTER){
    NavigationService.navigate("UserLoadingScreen")
  }

  const IsLoading = useSelector((state) => state.firebase.firebaseIsLoading)
  const firebaseErrorMessage = useSelector((state) => state.firebase.firebaseErrorMessage)
  
  const dispatch = useDispatch()
  useEffect(() => {
  }, [])

  return (
    <View
      style={[
        Helpers.mainContainer,
      ]}
    >
      <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
        {firebaseErrorMessage ? (
          <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
        ):(<></>)}

        <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
          <Input
            label="Farm Name"
            style={ApplicationStyles.input}
            onChangeText={text => {setFarm(text)}}
            value={Farm}
          />
          <Input
            label="Number of Sections"
            style={ApplicationStyles.input}
            onChangeText={text => {setSections(text)}}
            value={Sections}
          />
          <Input
            label="Number of Pens in Sections"
            style={ApplicationStyles.input}
            onChangeText={text => {setPens(text)}}
            value={Pens}
          />
        </View>
        <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
          <CustomButton Text="Create User" 
            onPress={() => {
              var _UserData = {}
              var _sections = {};
              var _pens = {};

              if (!Farm || Farm === ""){
                dispatch(FirebaseActions.setFirebaseError("Farm name is needed"))
              }
              else if (isNaN(Sections)){
                dispatch(FirebaseActions.setFirebaseError("Number of Sections have to be a number"))
              } else if (isNaN(Pens)){
                dispatch(FirebaseActions.setFirebaseError("Number of Pens in Sections have to be a number"))
              }
              else {
                _UserData = {state: UserStates.FREE, farms: {}}
                
                var Pen = 1;
                var i, i2;
                for (i = 1; i <= Sections; i++){
                  var sectionPens = {};
                  for (i2 = 1; i2 <= Pens; i2++){
                    _pens['Pen_' + Pen] = {section: 'Section_' + i}
                    sectionPens['Pen_' + Pen] = true
                    Pen++;
                  }
                  
                  _sections['Section_' + i] = {pens: sectionPens}
                }
                
                dispatch(FirebaseActions.updateUserData({userData: _UserData, farms:{name:Farm}, sections:_sections, pens:_pens}))
              }
            }}/>
          </View>
      </View>
      {IsLoading &&
        <ActivityIndicator style={Helpers.loadingContainer} size='large' />
      }
    </View>
  );
}

export default CreateNewUser;
